import React, { useEffect, useState } from "react";
import { api } from "../api/api";
import { Card } from "../components/movie/card.jsx";
import { useLocation } from "react-router-dom";
import { GenreCard } from "../components/genre/genre_card";
import "../App.css";
import jwt_decode from "jwt-decode";
import { DetailPage } from "./detailPage";
import Pusher from "pusher-js";

export const HomeScreen = ({
  currentOtt,
  setcurrentOtt,
  recommendation_data,
  setrecommendation_data,
}) => {
  var token = localStorage.getItem("token");
  var pid = "3a0d184c08f8485d9fcdd0e2ced4bb58";
  useEffect(() => {
    if (token === null) {
      pid = "3a0d184c08f8485d9fcdd0e2ced4bb58";
    } else {
      var decoded = jwt_decode(token);
      pid = decoded["profile_id"];
      console.log("ddd", pid);
    }
    const pusher = new Pusher("fe87c8d00d6367cc501c", {
      cluster: "ap2",
    });

    var channel = pusher.subscribe(pid);
    console.log("pid pusher", pid);
    channel.bind("data", function(data) {
      console.log(data.message);
      window.location.reload();
    });
    let url = "/recommendation/recommend/?user_id=" + pid;
    api
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        if (data) {
          console.log(data.length);
          setrecommendation_data(data);
        } else {
          data = [];
        }
      });
    return () => {};
  }, []);

  return (
    <>
      <GenreCard setrecommendation_data={setrecommendation_data} />
      <DetailPage currentOtt={currentOtt} setcurrentOtt={setcurrentOtt} />
      <div
        className="recommendation_row"
        style={{
          display: "flex",
          columnGap: "10px",
          flexWrap: "wrap",
          margin: "20px",
          justifyContent: "space-evenly",
          rowGap: "10px",
        }}
      >
        {recommendation_data.length >= 0 &&
          recommendation_data.map((data) => {
            return (
              <Card
                key={data.tmdb_id}
                currentOtt={currentOtt}
                setcurrentOtt={setcurrentOtt}
                image={data.poster_path}
                tmdb_id={data.tmdb_id}
                imdb_id={data.imdb_id}
                title={data.title}
                release_date={data.release_date}
                description={data.overview}
                vote_average={data.vote_average}
                backdrop={data.backdrop_path}
              />
            );
          })}
      </div>
    </>
  );
};
