import React, { useRef } from "react";
import "./model.css";
export const LoginModel = ({ show }) => {
  const token = useRef();
  const handleLogin = () => {
    localStorage.setItem("token", token.current.value);
  };

  return (
    <>
      {show && (
        <>
          <div className="login_model">
            <textarea
              ref={token}
              type="text"
              placeholder="enter the token"
              cols="80"
            ></textarea>
            <button
              onClick={() => {
                handleLogin();
              }}
            >
              login
            </button>
          </div>
        </>
      )}
    </>
  );
};
