import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { useLocation } from "react-router-dom";
import { api } from "../api/api";
import { TorrentCard } from "../components/torrent/card";
import "./master.css";

export const DetailPage = ({ currentOtt, setcurrentOtt }) => {
  const [torrents, settorrents] = useState([]);

  const handleClose = () => {
    var dp = document.getElementsByClassName("detail_page")[0];
    if (dp) {
      if (dp.style.display == "none") {
        dp.style.display = "block";
      } else {
        dp.style.display = "None";
      }
    }
  };

  useEffect(() => {
    console.log("imdb", currentOtt.imdb_id);
    let url = "/recommendation/torrent/?imdb_id=" + currentOtt.imdb_id;
    api
      .get(url)
      .then((response) => {
        console.log(response.status);
        if (response.status == 200) {
          return response.data;
        } else {
          return [];
        }
      })
      .then((data) => {
        console.log(data);
        if (data) {
          settorrents(data);
        }
      });
    return () => {};
  }, [currentOtt]);

  return (
    <div
      className="detail_page"
      style={{ display: "none" }}
      onClick={() => {
        handleClose();
      }}
    >
      <div className="detail_inner">
        <img
          src={"https://image.tmdb.org/t/p/original/" + currentOtt.backdrop}
          style={{ height: "650px", width: "100%" }}
        ></img>
        <div
          style={{
            height: "652px",
            width: "100%",
            backgroundColor: "#00000096",
            position: "relative",
            top: "-653px",
          }}
        ></div>
        <div className="" style={{}}>
          <img
            src={"https://image.tmdb.org/t/p/w500/" + currentOtt.image}
            style={{
              borderRadius: "10px",
              height: "300px",
              position: "absolute",
              top: "365px",
              left: "85px",
            }}
          ></img>
          <p className="ott_details title">{currentOtt.title}</p>
          <p className="ott_details vote"> {currentOtt.vote_average}</p>
          <p className="ott_details release">{currentOtt.release_date}</p>
          <p className="ott_details description">{currentOtt.description}</p>
        </div>
        <div
          className="torrent-cards"
          style={{
            position: "absolute",
            top: "670px",
            left: "315px",
            display: "flex",
            flexDirection: "row",
            columnGap: "15px",
          }}
        >
          {torrents.length > 0 &&
            torrents.map((data) => {
              return (
                <TorrentCard
                  size={data.size}
                  size_bytes={data.size_bytes}
                  type={data.type}
                  url={data.url}
                  seeds={data.seeds}
                ></TorrentCard>
              );
            })}
        </div>
      </div>
    </div>
  );
};
